body{
    background-color: black;
}
.inputbar{
    background-color: aliceblue;
    width: 600px;
    height: 40px;
    text-align: center;
    font-size: 23px;
    margin-top: 200px;
    border-radius: 6px;
    border: 0px;
    text-decoration: none;
    font-weight: 0;
    border-style: none;

}
img{
    width:120px;
    height: 50px;
}
.navbar{
    background-color:#adff2f;
    height: 50px;
    text-align: center;
}
.logout{
    background-color: black;
    color:white;
    position: absolute;
    right: 0;
    margin: 20px;
}
button{
    width: 130px;
    height: 43px;
    background-color: greenyellow;
    border-radius: 6px;
    font-size: 20px;
    cursor: pointer;
    text-decoration: none;
    font-weight: 0;
    border-style: none;
    border: 0px;
}
.url{
    color:grey;
    margin-left: -1px;
    font-size: 30px;
}

.copybut{
    width:100px;
    text-align: center;
    padding-bottom: 2px;
}

.dash h2{
    color:greenyellow;
}
.dash h3{
    color:white;
    margin-top: 20px;
}

@media screen and (max-width: 700px) {
    .inputbar{
        width: 310px;
        margin-left: 10px;
    }
    button{
        margin-top: 15px;
        margin-left: -8px;
    }
    .url{
        font-size: 16px;
    }
    .copybut{
       width: 70px;
       height: 40px;
    }
    
  }
